.swatch {
  width: 22px;
  height: 22px;
  border-radius: 2px;
  border: 1px solid #fff;
  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.1), inset 0 0 0 1px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  transition: border-color 0.2s linear, background-color 0.2s linear;
  margin: 8px auto;
}

.swatch:hover{
  border-color: #2F90FF;
}

.swatch-disabled {
  width: 22px;
  height: 22px;
  border-radius: 2px;
  border: 1px solid #f0f0f0;
  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.1), inset 0 0 0 1px rgba(0, 0, 0, 0.1);
  cursor: not-allowed;
  transition: border-color 0.2s linear, background-color 0.02s linear;
  margin:8px auto;
}

.hex-input {
  width: 100%;

  text-transform: uppercase;

  text-align: center;
  margin-top: 16px;
  
  outline: none;

  border: none;
  background-color: #FAFAFA;
  border-bottom: 1px solid #D9D9D9;

  transition: border-color 0.2s linear, background-color 0.02s linear;
}

.hex-input:focus{
  border-bottom: 1px solid #91D5FF;
  background-color: #E6F6FF;
}